.checkout{
    display: flex;
    padding-bottom: 5rem;
    justify-content: space-between;
}
.pageName{
    display: none;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 2.8rem;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
}

 .checkoutBasket{
    width: 44.7rem;
    background-color: #FFFFFF;
}
/*---------------------------------------------*/
.overlay{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,.6);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    z-index: 10;
}

.overlay.overlayVisible{
    visibility: visible;
    opacity: 1;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.overlay .checkoutBasket.checkoutBasketPopup{
    position: absolute;
    width: 37rem;
    height: 100vh;
    z-index: 12;
    top: 0;
    transform: translateX(100%);
    right: 0;
    transition: transform 0.3s ease-in-out;
}

.overlayVisible .checkoutBasket.checkoutBasketPopup{
    transform: translateX(0);
    transition: transform 0.3s ease-in-out;

}

.overlay .checkoutBasket.checkoutBasketPopup .basketItem{
    padding-bottom: 0;
}

.overlay .checkoutBasket.checkoutBasketPopup .basketInner{
    margin-top: 3rem;
    margin-bottom: 0;
    height: 55vh;
    width: 100%;
    padding-right: 3rem;
    scrollbar-width: thin;
    scrollbar-color: #212121 #ffff;
}
.basketInner::-webkit-scrollbar {
    width: 6px;
}
 .basketInner::-webkit-scrollbar-track {
    background: #ffffff;
}
.basketInner::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    border-radius: 10px;
    border: 1px solid #ffffff;
}

.overlay .checkoutBasket.checkoutBasketPopup .priceInfo{
    display: none;
}

.overlay .checkoutBasket.checkoutBasketPopup .priceTotal{
    margin-left: 3.8rem;
}
.overlay .checkoutBasket.checkoutBasketPopup .priceTotal p:first-child{
    margin-right: 9rem;
}
.overlay .checkoutBasket.checkoutBasketPopup .submitBtn{
    display: none;
}
.overlay .checkoutBasket.checkoutBasketPopup .checkoutLink{
    display: flex;
    margin: 4rem auto 0;
    width: 26rem;
}
.overlay .checkoutBasket.checkoutBasketPopup .closeBtn{
    display: flex;
}
 .closeBtn{
    display: none;
    margin: 2.5rem auto;
    align-items: center;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
.closeBtnImg{
    width: 3.5rem;
    height: 4.2rem;
}
.closeBtnText{
    margin-left: 1.5rem;
    color: #000;
    font-size: 1.8rem;
    font-family: Poppins, sans-serif;
    text-transform: uppercase;
}
.overlay .checkoutBasket.checkoutBasketPopup .info{
    display: flex;
}
 .info{
    display: none;
    align-items: center;
    justify-content: center;

}
.info h2{
    color: #000;
    font-size: 2.8rem;
    font-family: Poppins, sans-serif;
    text-transform: uppercase;
}
.info span{
    margin-left: 2.4rem;
    width: 7.2rem;
    height: .2rem;
    background-color: black;
    font-size: 0;
    color: transparent;
}
/*-------------------------------------------*/

.basketInner{
    margin-top: 10rem;
    margin-bottom: 5rem;
    width: 34.7rem;
    height: 53.3rem;
    font-family: 'Nunito', sans-serif;
    overflow-y:auto;
}
.emptyBasket{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.emptyBasketImage{
    width: 10rem;
    height: 10.5rem;
}
.emptyBasketText{
    margin: 8rem 2.9rem 0;
    text-align: center;
    font-size: 22px;
    font-family: Poppins, sans-serif;
}
.basketItem{
    position: relative;
    margin-bottom: 1rem;
    padding: 1.3rem 2rem 4rem 1.5rem;
    display: flex;

}
.deliteItem{
    position: absolute;
    top: 0.1rem;
    right:0.1rem;
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    border: .05rem solid #3C3C3C;
    border-radius: 50%;
    cursor: pointer;
    background-color: transparent;
}
.imgItem{
    margin-right: 1.4rem;
    width: 14rem;
    height: 16rem;
}
.infoItem{
    width: 15.8rem;
}
.nameItem{
    height: 4rem;
    font-weight: 400;
    font-size: 1.1rem;
    line-height: 180%;
    text-transform: uppercase;
}
.labelInfo{
    margin-top: 0.6rem;
    margin-bottom: 0.35rem;
    font-weight: 300;
    font-size: 1.4rem;
    line-height: 1.9rem;
}
.sizeMark{
    margin-right: .3rem;
    font-weight: 300;
    font-size: 1.4rem;

}
.quantityBlock{
    display: flex;
    align-items: center;
    font-weight: 300;
    font-size: 1.4rem;
}
.quantity{
    margin-right: 1.5rem;
    padding-right: 1.5rem;
    border-right: .05rem solid #3C3C3C;
}
.quantitySet{
    margin-right: 0.5rem;
    font-weight: 500;
    font-size: 2rem;
    cursor: pointer;

}
.priceItem{
    display: flex;
    justify-content: flex-end;
    font-weight: 300;
    font-size: 1.4rem;
}
.priceInfo{
    margin-bottom: 2rem;
    display: flex;
    font-family: 'Nunito', sans-serif;
    font-weight: 400;
    font-size: 1.4rem;

}

.priceInfo p{
    margin-right: 2rem;
    font-family: 'Poppins', sans-serif;
}
.priceTotal{
    padding-top:2rem ;
    display: flex;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 2.8rem;
    text-transform: uppercase;
}

.priceTotal p{
    margin-right: 2rem;
    font-size: 1.8rem;
}
.btn{
    margin-top: 4rem;
    width: 34.7rem;
    height: 4.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3C3C3C;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 2.1rem;
    text-transform: uppercase;
    color: #FFFFFF;
    cursor: pointer;
    text-decoration: none;
    transition: background-color 0.5s ease-out;
    border: none;
}
.btn:hover{
    background-color: #959595;
    transition: background-color 0.5s ease-out;
}
.checkoutLink{
    display: none;
}
@media(min-width: 1600px){
    .closeBtn{
        margin:2rem auto;
    }
    .closeBtnImg{
        width: 2.5rem;
        height: 3rem;
    }
    .closeBtnText{
        font-size: 1.6rem;
    }
    .info h2{
        font-size: 2.2rem;
    }
    .overlay .checkoutBasket.checkoutBasketPopup .basketInner{
        margin-top: 1rem;
    }
    .overlay .priceTotal{
        font-size: 2.6rem;
    }
    .overlay .priceTotal p{
        font-size: 1.6rem;
    }
}
@media (max-width: 1024px) {
    .btn:hover{
        background-color: #3C3C3C;
        transition: background-color 0s ease-out;
    }
}

@media (max-width: 768px) {
    .closeBtn{
        margin:1.6rem auto;
    }
    .checkout{
        justify-content: space-between;
        flex-direction: column;
        padding: 0 2rem;
    }
    .overlay .checkoutBasket.checkoutBasketPopup .basketInner{
        margin-top: 1.5rem;
    }
    .overlay .checkoutBasket.checkoutBasketPopup .checkoutLink{
        margin: 2.5rem auto 0;
    }
    .pageName{
        margin-top: 3rem;
        display: block;
    }

    div.checkout div.checkoutBasket{
        width: 100%;
        background-color: #FFFFFF;
    }

    .checkout .basketInner{
        display: none;
    }

    .priceInfo.delivery{
        margin-bottom: 0;
    }

    .priceInfo p{
        margin-right: 2rem;
        font-family: 'Poppins', sans-serif;
    }
    .priceTotal{
        font-size: 2.4rem;
    }
    .btn{
        margin-top: 3rem;
        margin-bottom: 7.5rem;
        width: 100%;
    }
}